"use client"

import { useState } from "react"
import { runTemplate } from "@/actions/template-actions"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog"
import { Checkbox } from "@/components/ui/checkbox"
import { Play, Loader2 } from "lucide-react"

export function ApplyTemplateDialog({ group, projects }: { group: { id: string; name: string }, projects: any[] }) {
    const [open, setOpen] = useState(false)
    const [pending, setPending] = useState(false)
    const [selectedProjectIds, setSelectedProjectIds] = useState<string[]>([])

    const toggleProject = (id: string) => {
        setSelectedProjectIds(prev =>
            prev.includes(id)
                ? prev.filter(p => p !== id)
                : [...prev, id]
        )
    }

    const toggleAll = () => {
        if (selectedProjectIds.length === projects.length) {
            setSelectedProjectIds([])
        } else {
            setSelectedProjectIds(projects.map(p => p.id))
        }
    }

    async function handleSubmit(formData: FormData) {
        if (selectedProjectIds.length === 0) return

        setPending(true)
        formData.append("groupId", group.id)
        formData.append("projectIds", JSON.stringify(selectedProjectIds))

        try {
            const result = await runTemplate(formData)
            if (result?.error) {
                console.error(result.error)
            } else {
                setOpen(false)
                setSelectedProjectIds([])
            }
        } catch (e) {
            console.error(e)
        } finally {
            setPending(false)
        }
    }

    return (
        <Dialog open={open} onOpenChange={setOpen}>
            <DialogTrigger asChild>
                <Button variant="outline" size="sm" className="gap-2">
                    <Play className="h-4 w-4" /> Run Template
                </Button>
            </DialogTrigger>
            <DialogContent className="sm:max-w-[500px]">
                <DialogHeader>
                    <DialogTitle>Run "{group.name}"</DialogTitle>
                    <DialogDescription>
                        Select projects to apply this template to. Tasks will be assigned based on project roles.
                    </DialogDescription>
                </DialogHeader>
                <form action={handleSubmit} className="space-y-4">
                    <div className="space-y-2">
                        <div className="flex items-center justify-between">
                            <Label>Projects ({selectedProjectIds.length} selected)</Label>
                            <Button type="button" variant="ghost" size="sm" onClick={toggleAll} className="h-auto p-0 text-xs">
                                {selectedProjectIds.length === projects.length ? "Deselect All" : "Select All"}
                            </Button>
                        </div>

                        <div className="border rounded-md p-2">
                            <div className="h-[200px] overflow-y-auto pr-2">
                                <div className="space-y-2">
                                    {projects.map((p) => (
                                        <div key={p.id} className="flex items-center space-x-2 p-1 hover:bg-muted/50 rounded">
                                            <Checkbox
                                                id={p.id}
                                                checked={selectedProjectIds.includes(p.id)}
                                                onCheckedChange={() => toggleProject(p.id)}
                                            />
                                            <label
                                                htmlFor={p.id}
                                                className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70 w-full cursor-pointer"
                                            >
                                                {p.name}
                                            </label>
                                        </div>
                                    ))}
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="space-y-2">
                        <Label htmlFor="startDate">Start Date</Label>
                        <Input id="startDate" name="startDate" type="date" required defaultValue={new Date().toISOString().split('T')[0]} />
                    </div>

                    <Button type="submit" className="w-full" disabled={pending || selectedProjectIds.length === 0}>
                        {pending ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : "Run Template"}
                    </Button>
                </form>
            </DialogContent>
        </Dialog>
    )
}
